.NumberInput {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
    width: 100%;
}

.NumberInput label {
    font-size: 15px;
    margin-bottom: 10px;
}

.NumberInput input {
    width: 100%;
    height: 30px;
    border-radius: 5px;
    border: 1px solid #222;
}

.NumberInput p {
    color: red;
    display: none;
}

.NumberInput.Wrong p {
    display: block;
}
